<template>
  <section>
    <h1>Private Area</h1>
    <p>This is your private area, enjoy!</p>

    <table>
      <tr>
        <th>Email Address</th>
        <td>
          <input type="email" name="email" :value="uploader.email" readonly />
        </td>
        <td>
          <button @click="changeEmail()">Edit</button>
        </td>
      </tr>
      <tr>
        <th>Name and Surname</th>
        <td>
          <input type="text" name="nomeCognome" :value="uploader.nomeCognome" readonly />
        </td>
        <td>
          <button @click="changeNomeCognome()">Edit</button>
        </td>
      </tr>
      <tr>
        <th>Password</th>
        <td>
          <input type="password" name="password" :value="uploader.password" readonly />
        </td>
        <td>
          <button @click="changePassword()">Edit</button>
        </td>
      </tr>
      <tr>
        <td></td>
        <td>
          <button @click="deleteChanges()">Delete</button>
        </td>
        <td>
          <input type="submit" @click="persistChanges()" value="Confirm Changes" />
        </td>
      </tr>
    </table>

    <form
      action="http://localhost:8080/ProgettoTomCat/AddLogo"
      method="POST"
      enctype="multipart/form-data"
    >
      <p>
        Do you want add or change your profile picture?&nbsp;
        <label for="profile_picture_file">
          Click here
          <input
            type="file"
            id="profile_picture_file"
            name="logo"
            @change="showPicture()"
          />
        </label>
      </p>
      <div>
        <img v-if="profile_picture!==''" v-bind:src="profile_picture" alt="new profile picture" />
        <label v-if="profile_picture!==''" for="submit_prof_pic">
          Do you really wanna set this image as profile picture?
          <input
            v-if="profile_picture!==''"
            id="submit_prof_pic"
            type="submit"
            value="CHANGE"
          />
        </label>
      </div>
    </form>
  </section>
</template>

<script scoped>
import Axios from "axios";

export default {
  name: "PrivateArea",
  methods: {
    showPicture: function () {
      let imageFile = document.querySelector("input[type='file']").files[0];
      let fileReader = new FileReader();
      let that = this;
      fileReader.readAsDataURL(imageFile);
      fileReader.onload = function () {
        that.profile_picture = fileReader.result;
      };
    },

    changeEmail: function () {
      let email = document.querySelector("input[type='email']");
      if (email.readOnly === true) {
        email.readOnly = false;
        email.style.backgroundColor = "white";
        email.style.border = "1px solid black";
        event.currentTarget.style.border = "2px inset";
      } else {
        email.readOnly = true;
        email.style.backgroundColor = "inherit";
        email.style.borderColor = "#F5F5F5";
        event.currentTarget.style.border = "2px outset";
      }
    },

    changeNomeCognome: function () {
      let nomeCognome = document.querySelector(`input[type="text"]`);
      if (nomeCognome.readOnly === true) {
        nomeCognome.readOnly = false;
        nomeCognome.style.backgroundColor = "white";
        nomeCognome.style.border = "1px solid black";
        event.currentTarget.style.border = "2px inset";
      } else {
        nomeCognome.readOnly = true;
        nomeCognome.style.backgroundColor = "inherit";
        nomeCognome.style.borderColor = "#F5F5F5";
        event.currentTarget.style.border = "2px outset";
      }
    },

    changePassword: function () {
      let password = document.querySelector("input[name='password']");
      if (password.readOnly === true) {
        password.type = "text";
        password.readOnly = false;
        password.style.backgroundColor = "white";
        password.style.border = "1px solid black";
        event.currentTarget.style.border = "2px inset";
      } else {
        password.readOnly = true;
        password.type = "password";
        password.style.backgroundColor = "inherit";
        password.style.borderColor = "#F5F5F5";
        event.currentTarget.style.border = "2px outset";
      }
    },

    deleteChanges: function () {
      document.querySelector(`input[name=email]`).value = this.uploader.email;
      document.querySelector(
        `input[name=nomeCognome]`
      ).value = this.uploader.nomeCognome;
      document.querySelector(
        `input[name=password]`
      ).value = this.uploader.password;
    },

    persistChanges: function () {
      this.uploader.email = document.querySelector(`input[name="email"]`).value;
      this.uploader.nomeCognome = document.querySelector(
        `input[name="nomeCognome"]`
      ).value;
      this.uploader.password = document.querySelector(
        `input[name="password"]`
      ).value;
      Axios.put(
        "http://localhost:8080/ProgettoTomCat/api/uploaderarea/info",
        this.uploader
      )
        .then((response) => {
          this.uploader = response.data;
          alert("Modifiche apportate con successo!");
        })
        .catch(() => alert("Dio ga' qualcosa è andato storto..."));
    },
  },

  data: function () {
    return {
      uploader: {},
      profile_picture: "",
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/uploadermanagment`)
      .then((response) => (this.uploader = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
}

table {
  width: 60%;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  background-color: inherit;
  border: 1px solid whitesmoke;
  border-radius: 5px;
}

button {
  border: 3px outset;
}

button:focus {
  outline-width: 0;
}

button:active {
  border-style: inset;
}

input[type="submit"] {
  color: white;
  background-color: blue;
  border: 3px solid black;
  border-radius: 5px;
}

input[type="submit"]:hover {
  cursor: pointer;
}

form {
  margin-top: 10px;
}

form input[type="file"] {
  display: none;
}
form label[for="profile_picture_file"] {
  color: blue;
}

form label[for="profile_picture_file"]:hover {
  text-decoration-line: underline;
  cursor: pointer;
}

form div {
  margin-top: 20px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
}

form img {
  max-height: 40vh;
  width: 40vh;
  height: 40vh;
  display: block;
  border: 4px solid black;
  border-radius: 20vh;
}

form label[for="submit_prof_pic"] {
  display: block;
}

form input[type="submit"] {
  display: block;
  margin-top: 10px;
  margin-right: auto;
  margin-left: auto;
  width: 80%;
  height: 50px;
  font-size: 1.5em;
}
</style>